import { BASE_URL } from "../constants"
import { LinkUpEntity, FormLinkUpEntity } from "../entities/LinkUpEntity"
import FetchUtils from "../utils/fetch"

interface AddLinkUpData {
    link_up: string,
    business_unit_id: number,
    link_up_warn: number,
    link_up_alarm: number
}

interface UpdateLinkUpData {
    link_up?: string
    link_up_warn?: number
    link_up_alarm?: number
    business_unit?: string
    business_unit_id?: number
}

class LinkUpService {
    static async GetAllLinkUp(business_unit_id?: string): Promise<LinkUpEntity[]> {
        let uri = `${BASE_URL}/api/v1/link_up`
        if (business_unit_id != null){
            uri += `?business_unit_id=${business_unit_id}`
        }

        const resp = await FetchUtils.fetchAuth(uri)
        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        const rawData = Array.isArray(resp.data) ? resp.data : []
        const data: LinkUpEntity[] = []
        for (let i = 0; i < rawData.length; i++) {
            data.push({
                link_up_id: rawData[i].link_up_id,
                link_up: rawData[i].link_up,
                link_up_warn: rawData[i].link_up_warn,
                link_up_alarm: rawData[i].link_up_alarm,
                business_unit: rawData[i].business_unit,
            })
        }

        return data
    }

    static async AddLinkUp(data: AddLinkUpData): Promise<{ message: string }> {
        let resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/v1/link_up`,
            {
                method: 'POST',
                body: JSON.stringify({ ...data }),
                headers: { "Content-Type": "application/json" }
            }
        )

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }

    static async UpdateLinkUp(link_up_id: number, fields: UpdateLinkUpData): Promise<{ message: string }> {
        let resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/v1/link_up`,
            {
                method: 'PATCH',
                body: JSON.stringify({ link_up_id: link_up_id, fields }),
                headers: { "Content-Type": "application/json" }
            }
        )

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }

    static async DeleteLinkUp(link_up_id: number): Promise<{ message: string }> {
        let resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/v1/link_up`,
            {
                method: 'DELETE',
                body: JSON.stringify({ link_up_id: link_up_id }),
                headers: { "Content-Type": "application/json" }
            }
        )

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }
}

export { LinkUpService }