import { useState, useEffect, useContext, useRef } from "react"
import AppContext from "../../context"
import css from "./Master.module.css"
import Card from "../../components/Card/Card"
import backgroundCard from "../../assets/images/backgroundCardBlue.jpg"
import Popup from "../../components/Popup/Popup"
import { FaEdit } from "react-icons/fa"
import { LinkUpEntity } from "../../entities/LinkUpEntity"
import { PopUpPilihEntity } from "../../components/Popup/PopupPilihEntity"
import PopupPilih from "../../components/Popup/PopupPilih"
import ExcelUtils from "../../utils/excel"
import { AlertEntity } from "../../layouts/Alert/AlertEntity"
import { FaSortDown, FaSortUp } from "react-icons/fa6"
import filterUtils from "../../utils/filter-utils"
import { GrPowerReset } from "react-icons/gr"
import { LinkUpService } from "../../services/link_up"
import PopupInputData from "../../components/InputData/InputDataElement"
import { ElectricEntity, FormElectric } from "../../entities/ElectricEntity"
import { ElectricService } from "../../services/electric"

export default function MasterElectric() {
    // ========================================= GLOBAL CONTEXT =========================================  //
    const context = useContext(AppContext)
    const contextVersion = context.version
    const contextAccessToken = context.contextAccessToken
    const contextShowAlertFunc = context.contextShowAlertFunc
    const contextShowLoadingFunc = context.contextShowLoadingFunc
    const stateShowLoading = context.stateShowLoading
    // ========================================= GLOBAL CONTEXT =========================================  //


    // ========================================= STATE VIEW =========================================  //
    const [tableData, setTableData] = useState<ElectricEntity[]>([])
    const [tableDataCache, setTableDataCache] = useState<ElectricEntity[]>([])

    const [selectedData, setSelectedData] = useState<ElectricEntity | null>(null)
    const [formData, setFormData] = useState<FormElectric | null>(null)

    const [showPopup, setShowPopup] = useState(false)
    const [showPopupPilih, setShowPopupPilih] = useState(false)
    const [popUpPilihEntity, setPopUpPilihEntity] = useState<PopUpPilihEntity>()

    const [sortColumn, setSortColumn] = useState<keyof ElectricEntity | null>(null)
    const [sortType, setSortType] = useState<"ascending" | "descending">("ascending")
    const [tableDataFilter, setTableDataFilter] = useState<{ [key: string]: string }>({})

    // ========================================= STATE VIEW =========================================  //

    // ========================================= FUNCTIONS =========================================  //
    // ========================================= FUNCTIONS =========================================  //

    // TABLE AND FORMS //
    const handleSelectLinkUp = async () => {
        contextShowLoadingFunc(true)
        setShowPopup(false)

        try {
            const resp = await LinkUpService.GetAllLinkUp()

            setPopUpPilihEntity(new PopUpPilihEntity({
                popupTitle: 'Select Link Up',
                tableContent: resp,
                onClick: (row: LinkUpEntity) => {
                    setFormData((prevState: FormElectric | null) => {
                        return { ...prevState, link_up_id: row.link_up_id, link_up: row.link_up }
                    })
                    setShowPopup(true)
                },
                onClose: () => { setShowPopupPilih(false); setShowPopup(true) }
            }))
            setShowPopupPilih(true)
        } catch (error: any) {
            contextShowAlertFunc(new AlertEntity({ message: error instanceof Error ? error.message : error.toString(), level: 3 }))
        } finally {
            contextShowLoadingFunc(false)
        }
    }

    const resetAllForm = () => {
        setFormData(null)
    }

    const handleShowPopup = (row?: ElectricEntity) => {
        setSelectedData(row ?? null)
        resetAllForm()
        setShowPopup(true)
    }

    const filterTable = (column: keyof ElectricEntity, columnnName?: string) => {
        return <div>
            <input style={{ fontSize: "12px", marginTop: "0.5dvh", maxWidth: "500px" }} type="text" value={tableDataFilter[column] ?? ""}
                placeholder={`${columnnName ?? column} ...`}
                onChange={(event) => {
                    setTableDataFilter((prev) => {
                        const newData = { ...prev }
                        newData[column] = event.target.value
                        return newData
                    })
                }}
            />
        </div>
    }

    const headerTable = (column: keyof ElectricEntity, columnName?: string) => {
        return <>
            <div style={{ display: "flex", flexDirection: "row", justifyContent: "center", gap: "3px", whiteSpace: "nowrap", cursor: "pointer" }}
                onClick={() => {
                    setSortColumn(column)
                    if (sortType === "ascending") {
                        setSortType("descending")
                    } else {
                        setSortType("ascending")
                    }
                }}
            >
                <div>{columnName ?? column}</div>
                <div style={{ display: "flex", flexDirection: "column", gap: "0", alignItems: "center", justifyItems: "center" }}
                >
                    <FaSortUp style={{ color: ((sortColumn == column && sortType == "descending") ? "var(--secondary-color)" : "silver"), margin: 0, padding: 0, height: '18px' }} />
                    <FaSortDown style={{ color: ((sortColumn == column && sortType == "ascending") ? "var(--secondary-color)" : "silver"), margin: "-18px", padding: 0, height: '18px' }} />
                </div>
            </div>
        </>
    }

    const handleFilterAndSort = async () => {
        const resp = tableDataCache
        let table_filtered = resp.filter((row) => {
            return filterUtils.FilterTable(row, tableDataFilter)
        }).sort((a, b) => {
            if (!sortColumn) return 0
            if (sortType == "ascending") {
                return filterUtils.SortingTable(a, b, sortColumn)
            } else {
                return filterUtils.SortingTable(b, a, sortColumn)
            }
        })
        setTableData(table_filtered)
    }

    useEffect(() => {
        handleFilterAndSort()
    }, [tableDataFilter, sortColumn, sortType])

    // TABLE AND FORMS //

    // CRUD SERVICES //
    const handleSaveAdd = async () => {
        contextShowLoadingFunc(true)

        try {
            if (formData == null) { throw new Error("Please fill all forms!") }

            if (formData.link_up_id == null) { throw new Error("Please fill link_up_id") }
            if (formData.parameter_name == null) { throw new Error("Please fill parameter_name") }
            if (formData.electric_tipe == null) { throw new Error("Please fill electric_tipe") }
            if (formData.electric_unit == null) { throw new Error("Please fill electric_unit") }
            if (formData.electric_display_type == null) { throw new Error("Please fill electric_display_type") }

            const resp = await ElectricService.AddElectric({
                link_up_id: formData.link_up_id,
                parameter_name: formData.parameter_name,
                electric_tipe: formData.electric_tipe,
                electric_warn: formData.electric_warn,
                electric_alarm: formData.electric_alarm,
                electric_bottom_warn: formData.electric_bottom_warn,
                electric_bottom_alarm: formData.electric_bottom_alarm,
                electric_unit: formData.electric_unit,
                electric_display_type: formData.electric_display_type,
            })

            await generateData()
            setSelectedData(null)
            resetAllForm()
            setShowPopup(false)
            contextShowAlertFunc(new AlertEntity({ message: resp.message, level: 1 }))
        } catch (error: any) {
            contextShowAlertFunc(new AlertEntity({ message: error instanceof Error ? error.message : error.toString(), level: 3 }))
        } finally {
            contextShowLoadingFunc(false)
        }
    }

    const handleSaveUpdate = async () => {
        contextShowLoadingFunc(true)

        try {
            if (selectedData == null) { throw new Error("Please fill in all forms!") }

            if (formData != null) {
                await ElectricService.UpdateElectric(selectedData.electric_id, formData)
            }

            await generateData()
            setSelectedData(null)
            resetAllForm()
            setShowPopup(false)
            contextShowAlertFunc(new AlertEntity({ message: "[Success]", level: 1 }))
        } catch (error: any) {
            contextShowAlertFunc(new AlertEntity({ message: error instanceof Error ? error.message : error.toString(), level: 3 }))
        } finally {
            contextShowLoadingFunc(false)
        }
    }

    const handleDelete = async () => {
        return contextShowAlertFunc(new AlertEntity({
            message: `Are you sure you want to delete ${selectedData?.parameter_name} ?`,
            onOk: async () => {
                contextShowLoadingFunc(true)

                try {
                    if (selectedData == null) return
                    const resp = await ElectricService.DeleteElectric(selectedData.electric_id)

                    await generateData()
                    setSelectedData(null)
                    resetAllForm()
                    setShowPopup(false)
                    contextShowAlertFunc(new AlertEntity({ message: resp.message, level: 1 }))
                } catch (error: any) {
                    contextShowAlertFunc(new AlertEntity({ message: error instanceof Error ? error.message : error.toString(), level: 3 }))
                } finally {
                    contextShowLoadingFunc(false)
                }
            },
            withCancel: true,
            withOk: true,
            level: 2
        }))
    }

    const generateData = async () => {
        contextShowLoadingFunc(true)
        try {
            const resp = await ElectricService.GetAllElectric()

            setTableData(resp)
            setTableDataCache(resp)
        } catch (error: any) {
            contextShowAlertFunc(new AlertEntity({ message: error instanceof Error ? error.message : error.toString(), level: 3 }))
        } finally {
            contextShowLoadingFunc(false)
        }
    }

    useEffect(() => {
        generateData()
    }, [contextAccessToken])

    // CRUD SERVICES //

    // IMPORT EXPORT EXCEL //
    const handleGenerateExcel = async () => {
        if (tableData.length == 0) { return contextShowAlertFunc(new AlertEntity({ message: "NO DATA!", level: 3 })); }

        const column = [
            "electric_id", "link_up_id", "link_up", "parameter_name",
            "electric_tipe", "electric_warn", "electric_alarm",
        ]
        let values: { [key: string]: string }[] = []
        for (let i = 0; i < tableData.length; i++) {
            values.push({
                electric_id: `${tableData[i].electric_id ?? ''}`,
                link_up_id: `${tableData[i].link_up_id ?? ''}`,
                link_up: `${tableData[i].link_up ?? ''}`,
                parameter_name: `${tableData[i].parameter_name ?? ''}`,
                electric_tipe: `${tableData[i].electric_tipe ?? ''}`,
                electric_warn: `${tableData[i].electric_warn ?? ''}`,
                electric_alarm: `${tableData[i].electric_alarm ?? ''}`,
            })
        }

        ExcelUtils.export(`Master Electric List`, column, values)
    }

    // IMPORT EXPORT EXCEL //

    // ========================================= FUNCTIONS =========================================  //
    // ========================================= FUNCTIONS =========================================  //

    if (contextVersion.electric == null) {
        return <div className="container" style={{ display: stateShowLoading ? "none" : "flex", backgroundColor: "transparent" }}>
            CENTERLINE IS DEACTIVATED
        </div>
    }

    return <>
        <div className="container" style={{ display: stateShowLoading ? "none" : "flex", backgroundColor: "transparent" }}>

            <div style={{ height: "94dvh", maxHeight: "100vh", display: "flex", flexDirection: "column" }}>

                {/* Card Header */}
                <div style={{ flexBasis: "auto" }} className="ordering-card">
                    <Card
                        detailTitle="Master Electric"
                        detailColor="var(--secondary-color)"
                        backgroundImage={backgroundCard}
                        stateShow={true}
                        detailCard={
                            <>
                                <div className={css.rowGrid} style={{ alignItems: 'end' }}>
                                    <div>
                                        <div style={{ color: "white", textAlign: "start" }}>Action :</div>
                                        <button className="cardButton" onClick={() => handleShowPopup()} >
                                            Add New
                                        </button>
                                    </div>
                                    <div>
                                        <button className="cardButton" onClick={() => handleGenerateExcel()} >
                                            Generate Excel
                                        </button>
                                    </div>
                                    <div>
                                        <button className="cardButton" onClick={() => { setTableDataFilter({}); generateData(); setSortColumn(null); setSortType("descending") }} >
                                            <GrPowerReset /> &nbsp; Reset Filter
                                        </button>
                                    </div>
                                </div>
                            </>
                        }
                    />
                </div>

                {/* Table */}
                <div style={{ flex: 1, overflowY: "auto" }} className="ordering-table">
                    <table className="tableclickcell">
                        <thead>
                            <tr>
                                <th colSpan={10}>MASTER ELECTRIC</th>
                                <th rowSpan={2} style={{ backgroundColor: "var(--header-color)", width: "7dvw", maxWidth: "7dvw", minWidth: "7dvw", position: "sticky", right: 0, zIndex: 1 }}>Action</th>
                            </tr>
                            <tr>
                                <th>
                                    {headerTable("electric_id", "Electric Id")}
                                    {filterTable("electric_id", "Electric Id")}
                                </th>
                                <th>
                                    {headerTable("link_up", "Link Up")}
                                    {filterTable("link_up", "Link Up")}
                                </th>
                                <th>
                                    {headerTable("parameter_name", "Parameter Name")}
                                    {filterTable("parameter_name", "Parameter Name")}
                                </th>
                                <th>
                                    {headerTable("electric_tipe", "Electric Tipe")}
                                    {filterTable("electric_tipe", "Electric Tipe")}
                                </th>
                                <th>
                                    {headerTable("electric_warn", "Electric Warn")}
                                    {filterTable("electric_warn", "Electric Warn")}
                                </th>
                                <th>
                                    {headerTable("electric_alarm", "Electric Alarm")}
                                    {filterTable("electric_alarm", "Electric Alarm")}
                                </th>
                                <th>
                                    {headerTable("electric_bottom_warn", "Electric Bottom Warn")}
                                    {filterTable("electric_bottom_warn", "Electric Bottom Warn")}
                                </th>
                                <th>
                                    {headerTable("electric_bottom_alarm", "Electric Bottom Alarm")}
                                    {filterTable("electric_bottom_alarm", "Electric Bottom Alarm")}
                                </th>
                                <th>
                                    {headerTable("electric_unit", "Electric Unit")}
                                    {filterTable("electric_unit", "Electric Unit")}
                                </th>
                                <th>
                                    {headerTable("electric_display_type", "Electric Display Type")}
                                    {filterTable("electric_display_type", "Electric Display Type")}
                                </th>
                            </tr>
                        </thead>
                        {tableData.length > 0
                            ? <tbody>
                                {tableData.map((row, index) => {
                                    return (
                                        <tr key={index}>
                                            <td>{row.electric_id}</td>
                                            <td>{row.link_up}</td>
                                            <td>{row.parameter_name}</td>
                                            <td>{row.electric_tipe}</td>
                                            <td>{row.electric_warn}</td>
                                            <td>{row.electric_alarm}</td>
                                            <td>{row.electric_bottom_warn}</td>
                                            <td>{row.electric_bottom_alarm}</td>
                                            <td>{row.electric_unit}</td>
                                            <td>{row.electric_display_type}</td>
                                            <td className={css.buttonFixAction} onClick={() => { handleShowPopup(row) }}>
                                                <FaEdit /> Edit
                                            </td>
                                        </tr>
                                    );
                                })}
                            </tbody>
                            : <tbody><tr><td colSpan={1000}>No Data Available Yet !</td></tr></tbody>
                        }
                    </table>
                </div>

            </div>


            {/* Popup Detail */}
            <Popup
                setShowPopup={setShowPopup}
                showPopup={showPopup}
                popupTitle={selectedData == null ? "Add New" : `Edit ${selectedData?.parameter_name}`}
                popupContent={<>
                    <div className={css.bodyPopup}>
                        <div className={css.containerForm}>

                            <div className={css.containerFormItem}>
                                <PopupInputData
                                    label={"Parameter Name"} type_input={"text"} value={formData?.parameter_name} original_value={selectedData?.parameter_name ?? ''}
                                    attribute={"parameter_name"} required={true} setFormData={setFormData}
                                />
                                <div>
                                    <div className={css.label}> Link Up <span style={{ color: "red" }}>*</span></div>
                                    <input className={css.inputedit} type="text" readOnly
                                        value={formData?.link_up ?? selectedData?.link_up ?? ""}
                                        onClick={handleSelectLinkUp}
                                    />
                                </div>
                                <div>
                                    <div className={css.label}> Electric Type <span style={{ color: "red" }}>*</span></div>
                                    <select
                                        className={css.inputedit}
                                        value={formData?.electric_tipe ?? selectedData?.electric_tipe ?? ''}
                                        onChange={(e) => {
                                            setFormData((prev) => ({ ...prev, electric_tipe: e.target.value }))
                                        }}
                                        required
                                    >
                                        <option value="" disabled>Select Type</option>
                                        <option value="Packer">Packer</option>
                                        <option value="Maker">Maker</option>
                                    </select>
                                </div>
                                <div>
                                    <div className={css.label}> Display Type <span style={{ color: "red" }}>*</span></div>
                                    <select
                                        className={css.inputedit}
                                        value={formData?.electric_display_type ?? selectedData?.electric_display_type ?? ''}
                                        onChange={(e) => {
                                            setFormData((prev) => ({ ...prev, electric_display_type: e.target.value }))
                                        }}
                                        required
                                    >
                                        <option value="" disabled>Select Display Type</option>
                                        <option value="gauge">Gauge</option>
                                        <option value="boolean">Boolean</option>
                                    </select>
                                </div>
                                <PopupInputData
                                    label={"Electric Warn"} type_input={"float"} value={formData?.electric_warn} original_value={selectedData?.electric_warn ?? ''}
                                    attribute={"electric_warn"} required={true} setFormData={setFormData}
                                />
                                <PopupInputData
                                    label={"Electric Alarm"} type_input={"float"} value={formData?.electric_alarm} original_value={selectedData?.electric_alarm ?? ''}
                                    attribute={"electric_alarm"} required={true} setFormData={setFormData}
                                />
                                <PopupInputData
                                    label={"Electric Bottom Warn"} type_input={"float"} value={formData?.electric_bottom_warn} original_value={selectedData?.electric_bottom_warn ?? ''}
                                    attribute={"electric_bottom_warn"} required={true} setFormData={setFormData}
                                />
                                <PopupInputData
                                    label={"Electric Bottom Alarm"} type_input={"float"} value={formData?.electric_bottom_alarm} original_value={selectedData?.electric_bottom_alarm ?? ''}
                                    attribute={"electric_bottom_alarm"} required={true} setFormData={setFormData}
                                />
                                <PopupInputData
                                    label={"Electric Unit"} type_input={"text"} value={formData?.electric_unit} original_value={selectedData?.electric_unit ?? ''}
                                    attribute={"electric_unit"} required={true} setFormData={setFormData}
                                />

                            </div>
                        </div>
                    </div>

                    <div className={css.footerPopup}>
                        <div className={css.buttons2}>
                            <div className={css.left}>
                                <button className="tertiaryButton" onClick={() => { setShowPopup(false) }} >
                                    Close
                                </button>
                            </div>
                            {selectedData &&
                                <div className={css.right}>
                                    <button className="secondaryButton" onClick={() => handleDelete()}>
                                        Delete
                                    </button>
                                </div>
                            }
                            <div className={css.right}>
                                <button className="primaryButton"
                                    onClick={() => { selectedData ? handleSaveUpdate() : handleSaveAdd() }} >
                                    Save
                                </button>
                            </div>
                        </div>
                    </div>
                </>
                }
            />

            {popUpPilihEntity != null
                ? <PopupPilih
                    showPopup={showPopupPilih}
                    setShowPopup={setShowPopupPilih}
                    popUpPilihEntity={popUpPilihEntity}
                />
                : <></>}


        </div>
    </>
}
