import React from "react"
import Backdrop from "../../layouts/Backdrop/Backdrop"
import popup from "./Popup.module.css"

export default function Popup(props: {
    popupTitle: string
    showPopup: boolean
    setShowPopup: (val: boolean) => void
    popupContent: React.JSX.Element
}) {
    const { popupTitle, showPopup, setShowPopup, popupContent } = props

    return (
        <>
            {showPopup && <Backdrop close={() => setShowPopup(false)} />}
            <div className={popup.modalmobile} id={showPopup ? undefined : popup.hide} style={{ maxWidth: '1200px' }}>
                <div>
                    <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between", borderBottom: "5px solid #2d545e", marginBottom: "1em" }}>
                        {popupTitle != null ? (
                            <h2>
                                {popupTitle.toUpperCase().replace("_", " ")}
                            </h2>
                        ) : (
                            <h2>
                                &nbsp;
                            </h2>
                        )}
                        <h2 onClick={() => setShowPopup(false)}  >X</h2>
                    </div>
                </div>
                <div>
                    {popupContent}
                </div>
            </div>
        </>
    )
}
