import { useRef } from "react"

const PopupInputData = (props: {
    label: string
    type_input: "text" | "number" | "float"
    value: string | number | null | undefined
    original_value: string | number | undefined
    required: boolean
    attribute: string
    onchange?: (event: React.ChangeEvent<HTMLInputElement>) => void
    setFormData?: (value: React.SetStateAction<object | null>) => void
    style?: React.CSSProperties
}) => {
    const textinputRef = useRef<HTMLInputElement | null>(null)

    const { label, type_input, value, original_value, required, attribute, onchange, setFormData, style } = props

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        let newValue: string | number | null = event.target.value

        if (type_input == "number") {
            const cleanedValue = newValue.toString().replace(/[^0-9]/g, '').slice(0, 11)
            newValue = cleanedValue === '' ? null : parseInt(cleanedValue)
        }

        if (type_input == "float") {
            let cleanedValue = (newValue ?? '').toString().slice(0, 11)
            if (cleanedValue.length > 0 && cleanedValue[cleanedValue.length - 1] != '.') {
                if (!isNaN(parseFloat(cleanedValue))) {
                    newValue = cleanedValue === '' ? null : parseFloat(cleanedValue)
                }
            }
        }

        if (setFormData) {
            setFormData((prev: object) => ({ ...prev, [attribute]: newValue, }))
        }
    }

    const handleInputFocus = () => {
        setTimeout(() => {
            textinputRef.current?.scrollIntoView({ behavior: 'smooth', block: 'center' })
        }, 300)
    }


    return <div>
        <div>{label} {required && <span style={{ color: "red" }}>*</span>}</div>
        <input
            type="text"
            style={{ marginBottom: "0", ...style }}
            value={value === null ? '' : value ?? original_value ?? ''}
            onFocus={handleInputFocus}
            onChange={onchange == null ? handleChange : onchange}
        />
    </div>
}

export default PopupInputData